<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:400px;float:left;">
            <el-input v-model="keywords" suffix-icon="el-icon-search" placeholder=""></el-input>
          </div>
          <div style="width:70px;float:left;margin-left:10px;">
            <el-button type="primary" icon="el-icon-search" @click="fn_loadTableList()"></el-button>
          </div>
          <div style="width:120px;float:right;">
            <el-button type="primary" @click="centerDialogVisible = true,dialogTitle='()'"></el-button>
          </div>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" :max-height='mainHeight-132'>
              <el-table-column type="selection" width="55">
              </el-table-column>
              <el-table-column prop="id" label="ID" width="120">
              </el-table-column>
              <el-table-column prop="userID" label="ID" width="120">
              </el-table-column>
              <el-table-column prop="userName" label="" width="120">
              </el-table-column>
              <el-table-column label="" width="350">
                <template slot-scope="props">
                  <div :title='props.row.title' class="row2" style='-webkit-box-orient: vertical;'>{{props.row.title}}</div>
                </template>
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="props">
                  <div :title='props.row.content' class="row2" style='-webkit-box-orient: vertical;'>{{props.row.content}}</div>
                </template>
              </el-table-column>
              <el-table-column label="" width="80">
                <template slot-scope="props">
                  {{ props.row.ishuifu == 0? "" : "" }}
                </template>
              </el-table-column>
              <el-table-column prop="answerName" label="ID" width="120">
              </el-table-column>
              <el-table-column label="" width="140">
                <template slot-scope="props">
                  {{ props.row.islike == 0? "" : "" }}
                </template>
              </el-table-column>
              <el-table-column prop="ndate" label="" width="120">
              </el-table-column>
              <el-table-column label="" width="100">
                <template slot-scope="props">
                  <!-- {{ props.row.suerType == 1? "" : "" }} -->
                  <el-tag type="warning" v-if='(props.row.isallot==0)' size='small'></el-tag>
                  <el-tag type="success" v-else size='small'></el-tag>
                </template>
              </el-table-column>
              <el-table-column label="" width="110" class-name='noneborder'>
                <template slot-scope="props">
                  <el-button size='small' type='primary' @click="openEditDialog(props.row),dialogTitle=''"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 50, 100]" :page-size="10" layout="total, sizes, prev, pager, next" :total="maxPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <el-dialog :title="dialogTitle" :visible.sync="centerDialogVisible" width="1000px" style='text-align:left' @close="hide" :close-on-click-modal='false'>
        <div>
          <div class="w-clear mb15">
            <div class="f-left-right50" style="width:150px;">ID{{id}}</div>
            <div class="f-left-right50">
              <div class="f-left">ID</div>
              <div class="f-left">
                <el-input type="text" v-model="userID" size="mini" placeholder=""></el-input>
              </div>
            </div>
            <div class="f-left">
              <div class="f-left"></div>
              <div class="f-left">
                <el-input type="text" v-model="userName" size="mini" placeholder=""></el-input>
              </div>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left">
              
            </div>
            <div class="f-left" style="width:870px;">
              <el-input type="text" v-model="title" placeholder="" size="mini"></el-input>
            </div>
          </div>
          <div class="w-clear mb15">
            <div></div>
            <div>
              <el-input type="textarea" :rows="4" placeholder="" v-model="content"></el-input>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left-right20">
              <div class="f-left">
                
              </div>
              <div class="f-left">
                <template>
                  <el-radio v-model="ishuifu" label="1"></el-radio>
                  <el-radio v-model="ishuifu" label="2"></el-radio>
                </template>
              </div>
            </div>

            <div class="f-left-right20">
              <div class="f-left">
                ID
              </div>
              <div class="f-left">
                <template>
                  <el-select v-model="value2" size="mini" placeholder="">
                    <el-option v-for="item in options2" :key="item.userID" :label="item.userName" :value="item.userID">
                    </el-option>
                  </el-select>
                </template>
              </div>
            </div>
            <div class="f-left-right20">
              <div class="f-left">
                 
              </div>
              <div class="f-left">
                <template>
                  <el-radio v-model="islike" label="1"></el-radio>
                  <el-radio v-model="islike" label="0"></el-radio>
                </template>
              </div>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left-right50">
              <div class="f-left"></div>
              <div class="f-left">
                <div class="block">
                  <el-date-picker v-model="ndate" align="right" type="date" size="mini" placeholder="">
                  </el-date-picker>
                </div>
              </div>
            </div>
            <div class="f-left-right">
              <div class="f-left">
                
              </div>
              <div class="f-left">
                <template>
                  <!-- {{ props.row.suerType == 1? "" : "" }} -->
                  <el-tag type="info" v-if='(isallot == 0)' size='medium'></el-tag>
                  <el-tag type="success" v-else size='medium'></el-tag>
                </template>
              </div>
            </div>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <div v-if="v">
            <el-button @click="centerDialogVisible = false"> </el-button>
            <el-button type="primary" @click="fn_add()"> </el-button>
          </div>
          <div v-if="!v">
            <el-button @click="centerDialogVisible = false"> </el-button>
            <el-button type="primary" @click="fn_allot()"> </el-button>
          </div>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
export default {
  props: {
    mainHeight: [Number, String],
    userType: Array
  },
  data() {
    return {
      v: true,
      Url: 'http://int.answer.updrv.com',
      dialogTitle: '',
      currentPage: 1, // 
      maxPage: 100,   // 
      pageArr: 10, // 
      initial: 20, // 
      centerDialogVisible: false,
      tableData: [],
      multipleSelection: [],
      keywords: '',
      options: [{
        value: '0',
        label: ''
      }, {
        value: '1',
        label: ''
      }],
      options2: [],
      value: '',
      value2: '',
      id: '', //id
      userID: '', //id
      userName: '', //
      title: '', //
      content: '', //
      ishuifu: '', //  0 1 
      answerName: '', //id (id-)
      islike: '', //  0  1
      ndate: '', //
      isallot: 0  //  0 1
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    handleSizeChange(val) {
      console.log(` ${val} `)
      this.pageArr = val
      this.fn_loadTableList()
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`)
      this.currentPage = val
      this.fn_loadTableList()
    },
    openEditDialog(row) {
      console.log(row)
      this.centerDialogVisible = true
      var val = ''
      for (var i = 0; i < this.options2.length; i++) {

        if (this.options2[i].userName == row.answerName) {
          val = this.options2[i].userID
        }

      }
      this.v = false
      // this.value2 = row.answerName.split('-')[0]
      this.value2 = row.answerName
      this.id = row.id //id
      this.userID = row.userID //id
      this.userName = row.userName //
      this.title = row.title //
      this.content = row.content //
      this.ishuifu = row.ishuifu + ''//  0 1 
      this.answerName = row.answerName //id (id-)
      this.islike = row.islike + '' //  0  1
      this.ndate = row.ndate //
      this.isallot = row.isallot  //  0 1
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    hide() {
      this.v = true
      this.value2 = ''
      this.id = '' //id
      this.userID = '' //id
      this.userName = '' //
      this.title = '' //
      this.content = '' //
      this.ishuifu = '' //  0 1 
      this.answerName = '' //id (id-)
      this.islike = '' //  0  1
      this.ndate = '' //
      this.isallot = 0  //  0 1
    },
    fn_deletes() {
      if (this.multipleSelection.length > 0) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '~'
        });
      }
    },
    fn_loadTableList() {
      var _this = this
      var keyword = this.keywords, pageNum = this.currentPage, pageSize = this.pageArr
      this.post("/json/companyList", {
        keyword,
        pageNum,
        pageSize
      }, (data) => {
        _this.tableData = data.data
        _this.maxPage = data.totalPage
      })
    },
    fn_add() {
      var _this = this
      // console.log(val)
      // var keyword = this.keywords, pageNum = this.currentPage
      var userName = this.userName,
        title = this.title,
        content = this.content,
        ndate = this.ndate
      this.post("/json/questAdd", {
        userName, // 
        title, // 
        content, // 
        ndate // 
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_allot() {
      var _this = this
      var qid = this.id, uid = this.value2
      this.post("/json/company/answerfenpei", {
        qid,
        uid
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_postion() {
      var _this = this
      this.post("/json/company/answerList", {
      }, (data) => {
        _this.options2 = data.data
      })
    }
  },
  mounted() {
    // console.log(1)
    window.vue = this
    this.fn_loadTableList()
    this.fn_postion()
  }
}
</script>
<style lang="scss" scoped>
  .box-orient{
    -webkit-box-orient: vertical;
  }
</style>


