<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:400px;float:left;">
            <el-input v-model="keywords" suffix-icon="el-icon-search" placeholder=""></el-input>
          </div>
          <div style="width:120px;float:left;margin-left:10px;">
            <template>
              <el-select v-model="value" placeholder="">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </template>
          </div>
          <div style="width:70px;float:left;margin-left:10px;">
            <el-button type="primary" icon="el-icon-search" @click="fn_loadTableList()"></el-button>
          </div>
          <div style="width:340px;float:right;">
            <el-button type="primary" @click="centerDialogVisible=true,dialogTitle='()'"></el-button>
            <el-button type="danger" @click="fn_repulse"></el-button>
            <el-button type="info" @click="fn_blacklist"></el-button>
          </div>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" :max-height='mainHeight-132'>
              <el-table-column type="selection" width="55">
              </el-table-column>
              <el-table-column prop="id" label="ID" width="80">
              </el-table-column>
              <el-table-column prop="userID" label="ID" width="80">
              </el-table-column>
              <el-table-column prop="userName" label="" width="80">
              </el-table-column>
              <el-table-column label="" width="80">
                <template slot-scope="props">
                  {{ props.row.isAnonymous == 1? "" : "" }}
                </template>
              </el-table-column>
              <el-table-column label="" width="300">
                <template slot-scope="props">
                  <div :title='props.row.title' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.title}}</div>
                </template>
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="props">
                  <div :title='props.row.content' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.content}}</div>
                </template>
              </el-table-column>
              <el-table-column prop="viewCount" label="" width="80">
              </el-table-column>
              <el-table-column prop="followCount" label="" width="80">
              </el-table-column>
              <el-table-column prop="answerCount" label="" width="80">
              </el-table-column>
              <el-table-column label="ID" width="140">
                <template slot-scope="props">
                  <div :title='props.row.answerID' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.answerID}}</div>
                </template>
              </el-table-column>
              <el-table-column label="ID" width="140">
                <template slot-scope="props">
                  <div :title='props.row.followID' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.followID}}</div>
                </template>
              </el-table-column>
              <el-table-column prop="ndate" label="" width="120">
              </el-table-column>
              <el-table-column label="" width="80">
                <template slot-scope="props">
                  <!-- {{ props.row.suerType == 1? "" : "" }} -->
                  <el-tag type="success" v-if='(props.row.status==1)' size='small'></el-tag>
                  <el-tag type="danger" v-else-if='(props.row.status==0)' size='small'></el-tag>
                  <el-tag type="info" v-else size='small'></el-tag>
                </template>
              </el-table-column>
              <el-table-column label="" width="90" class-name='noneborder'>
                <template slot-scope="props">
                  <div style="margin-bottom:10px;">
                    <el-button size="mini" @click="fn_check(props.row)" type="primary"></el-button>
                  </div>
                  <div>
                    <el-button @click="fn_look(props.row)" size="mini" type="info"></el-button>
                  </div>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 50, 100]" :page-size="10" layout="total, sizes, prev, pager, next" :total="maxPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <el-dialog :title="dialogTitle" :visible.sync="centerDialogVisible" width="1000px" style='text-align:left' @close="hide" :close-on-click-modal='false'>
        <div>
          <div class="w-clear mb15">
            <div class="f-left" style="width:150px;">ID{{id}}</div>
            <div class="f-left-right20">
              <div class="f-left">ID</div>
              <div class="f-left">
                <el-input v-model="userID" size="mini" :disabled="zhiDu" placeholder=""></el-input>
              </div>
            </div>
            <div class="f-left-right50">
              <div class="f-left"></div>
              <div class="f-left">
                <el-input v-model="userName" size="mini" :disabled="zhiDu" placeholder=""></el-input>
              </div>
            </div>
            <div class="f-left-right">
              <div class="f-left"></div>
              <div class="f-left">
                <el-radio v-model="isAnonymous" :disabled="zhiDu" label="0"></el-radio>
                <el-radio v-model="isAnonymous" :disabled="zhiDu" label="1"></el-radio>
              </div>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left-right50">{{viewCount}}</div>
            <div class="f-left-right50">{{followCount}}</div>
            <div class="f-left-right50">{{answerCount}}</div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left">
              
            </div>
            <div class="f-left" style="width:879px;">
              <el-input v-model="title" size="mini" :disabled="zhiDu" placeholder=""></el-input>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left">
              
            </div>
            <el-input v-model="content" :disabled="zhiDu" type="textarea" :rows="3" size="mini" placeholder=""></el-input>
          </div>
          <div class="w-clear mb15">
            <div class="f-left">
              ID
            </div>
            <el-input v-model="answerID" type="textarea" :disabled="zhiDu" :rows="3" size="mini" placeholder=""></el-input>
          </div>
          <div class="w-clear mb15">
            <div class="f-left">
              ID
            </div>
            <el-input v-model="followID" type="textarea" :disabled="zhiDu" :rows="3" size="mini" placeholder=""></el-input>
          </div>
          <div class="w-clear mb15">
            <div class="f-left" style="width:50%;">
              <div class="f-left">
                
              </div>
              <div class="f-left">
                <el-date-picker v-model="ndate" :disabled="zhiDu" align="right" type="date" placeholder="" size="mini">
                </el-date-picker>
              </div>
            </div>
            <div class="f-left" style="width:49%;">
              <div class="f-left">
                
              </div>
              <div class="f-left">
                <template>
                  <!-- {{ props.row.suerType == 1? "" : "" }} -->
                  <el-tag type="success" v-if='(status==1)' size='small'></el-tag>
                  <el-tag type="danger" v-else-if='(status==0)' size='small'></el-tag>
                  <el-tag type="info" v-else-if='(status==-1)' size='small'></el-tag>
                </template>
              </div>
            </div>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <div v-if="add">
            <el-button type="" @click="centerDialogVisible = false"> </el-button>
            <el-button v-if="add2" type="primary" @click="fn_add"> </el-button>
          </div>
          <div v-if="!add">
            <el-button type="" @click="centerDialogVisible = false"> </el-button>
            <el-button type="info" @click="fn_black"> </el-button>
            <el-button type="danger" @click="fn_check_to(0)"> </el-button>
            <el-button type="primary" @click="fn_check_to(1)"> </el-button>
          </div>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
export default {
  props: {
    mainHeight: [Number, String],
    userType: Array
  },
  data() {
    return {
      Url: 'http://int.answer.updrv.com',
      zhiDu: false, // 
      add: true,
      add2: true,
      dialogTitle: '',
      currentPage: 1, // 
      maxPage: 100,   // 
      pageArr: 10, // 
      initial: 20, // 
      centerDialogVisible: false,
      tableData: [],
      multipleSelection: [],
      keywords: '',
      options: [{
        value: '0',
        label: ''
      }, {
        value: '1',
        label: ''
      }],
      value: '',
      id: '',  //id
      userID: '', //id
      userName: '', //
      isAnonymous: 0, // 0  1
      title: '',  //
      content: '',  //
      viewCount: '', //
      followCount: '',  //
      answerCount: '', //
      answerID: '',  //id
      followID: '',  //id
      ndate: '',  //
      status: 0  //  0   1
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    handleSizeChange(val) {
      console.log(` ${val} `)
      this.pageArr = val
      this.fn_loadTableList()
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`)
      this.currentPage = val
      this.fn_loadTableList()
    },
    openEditDialog(row) {
      console.log(row)
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    hide() {
      this.zhiDu = false
      this.add = true
      this.add2 = true
      this.id = ''  //id
      this.userID = '' //id
      this.userName = '' //
      this.isAnonymous = '' // 0  1
      this.title = ''  //
      this.content = ''  //
      this.viewCount = '' //
      this.followCount = ''  //
      this.answerCount = '' //
      this.answerID = ''  //id
      this.followID = ''  //id
      this.ndate = ''  //
      this.status = ''  //  0   1
    },
    fn_repulse() {
      if (this.multipleSelection.length > 0) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var _this = this
          var id = []
          for (var i = 0, y = this.multipleSelection.length; i < y; i++) {
            id.push(this.multipleSelection[i].id)
          }
          this.post("/json/questauditList", {
            id
          }, (data) => {
            _this.fn_loadTableList()
            _this.centerDialogVisible = false
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '~'
        });
      }
    },
    fn_blacklist() {
      if (this.multipleSelection.length > 0) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var _this = this
          var id = []
          for (var i = 0, y = this.multipleSelection.length; i < y; i++) {
            id.push(this.multipleSelection[i].id)
          }
          this.post("/json/backquestList", {
            id
          }, (data) => {
            _this.fn_loadTableList()
            _this.centerDialogVisible = false
          })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '~'
        });
      }
    },
    fn_check(data) {
      this.dialogTitle = ''
      this.centerDialogVisible = true
      this.add = false
      console.log(data)
      this.id = data.id //id
      this.userID = data.userID //id
      this.userName = data.userName //
      this.isAnonymous = data.isAnonymous +'' // 0  1
      this.title = data.title  //
      this.content = data.content  //
      this.viewCount = data.viewCount //
      this.followCount = data.followCount  //
      this.answerCount = data.answerCount //
      this.answerID = data.answerID  //id
      this.followID = data.followID  //id
      this.ndate = data.ndate  //
      this.status = data.status  //  0   1
    },
    fn_look(data) {
      this.zhiDu = true
      this.add2 = false
      this.dialogTitle = ''
      this.centerDialogVisible = true
      this.id = data.id //id
      this.userID = data.userID //id
      this.userName = data.userName //
      this.isAnonymous = data.isAnonymous + '' // 0  1
      this.title = data.title  //
      this.content = data.content  //
      this.viewCount = data.viewCount //
      this.followCount = data.followCount  //
      this.answerCount = data.answerCount //
      this.answerID = data.answerID  //id
      this.followID = data.followID  //id
      this.ndate = data.ndate  //
      this.status = data.status  //  0   1
    },
    fn_add() {
      var _this = this
      // var keyword = this.keywords, pageNum = this.currentPage
      var userName = this.userName,
        isAnonymous = this.isAnonymous,
        title = this.title,
        content = this.content,
        ndate = this.ndate
      this.post("/json/questAdd", {
        userName, // 
        isAnonymous, // 
        title, // 
        content, // 
        ndate // 
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_check_to(status) {
      var _this = this
      var id = this.id
      this.post("/json/questAudit", {
        id,
        status
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_black() {
      var _this = this
      var id = this.id
      this.post("/json/backquest", {
        id
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_loadTableList() {
      var _this = this
      // var keyword = this.keywords, pageNum = this.currentPage
      var pageNum = this.currentPage, pageSize = this.pageArr, keyword = this.keywords, status = this.status
      this.post("/json/questList", {
        pageNum, //,
        pageSize,//,
        keyword,//,
        status//
      }, (data) => {
        _this.tableData = data.data
        _this.maxPage = data.totalPage
      })
    }
  },
  mounted() {
    // console.log(1)
    window.vue = this
    this.fn_loadTableList()
  }
}
</script>
<style lang="scss" scoped>
  .box-orient{
    -webkit-box-orient: vertical;
  }
</style>




