<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:400px;float:left;">
            <el-input v-model="keywords" suffix-icon="el-icon-search" placeholder="ID////"></el-input>
          </div>

          <div style="width:200px;float:left;margin-left:10px;">
            <template>
              <el-select v-model="value" placeholder="">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </template>
          </div>
          <div style="width:70px;float:left;margin-left:10px;">
            <el-button type="primary" icon="el-icon-search" @click="fn_loadTableList"></el-button>
          </div>
          <div style="width:360px;float:right;">
            <el-button type="primary" @click="centerDialogVisible=true,dialogTiele=''"></el-button>
            <el-button type="danger" @click="fn_deletes()"></el-button>
            <el-button type="warning" @click="excel"></el-button>
          </div>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" :max-height='mainHeight-132'>
              <el-table-column type="selection" width="55">
              </el-table-column>
              <el-table-column prop="id" label="ID" width="80">
              </el-table-column>
              <el-table-column prop="userName" label="" width="160">
              </el-table-column>
              <el-table-column prop="nickName" label="" width="160">
              </el-table-column>
              <el-table-column prop="phone" label="" width="160">
              </el-table-column>
              <el-table-column prop="email" label="" width="300">
              </el-table-column>
              <el-table-column label="" width="120">
                <template slot-scope="props">
                  {{userType[props.row.userType]}}
                </template>
              </el-table-column>
              <el-table-column label="" width="80">
                <template slot-scope="props">
                  {{ props.row.isAnswerer == 1? "" : props.row.isAnswerer == 2 ? "" : '' }}
                </template>
              </el-table-column>
              <el-table-column label="" width="150">
                <template slot-scope="props">
                  {{ props.row.isAudit == 1? "" : props.row.isAudit == 2 ? '' : ''}}
                </template>
              </el-table-column>
              <el-table-column label="" width="80">
                <template slot-scope="props">
                  <el-tag type="success" v-if='(props.row.status==0)' size='small'></el-tag>
                  <el-tag type="danger" v-else size='small'></el-tag>
                </template>
              </el-table-column>
              <el-table-column prop="addTime" label="" width="160">
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="props">
                  <div :title='props.row.remark' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.remark}}</div>
                </template>
              </el-table-column>
              <el-table-column label="" width="120" class-name='noneborder'>
                <template slot-scope="props">
                  <div style="margin-bottom:10px;">
                    <el-button size="mini" @click="redact(props.row)" type="primary"></el-button>
                  </div>
                  <div>
                    <el-button @click="fn_delete(props.row.id)" size="mini" type="danger"></el-button>
                  </div>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 50, 100]" :page-size="10" layout="total, sizes, prev, pager, next" :total="maxPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <el-dialog :title="dialogTiele" :visible.sync="centerDialogVisible" width="1000px" style='text-align:left' @close="resetForm('ruleForm'),hide()" :close-on-click-modal='false'>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <div class="w-clear">
            <div class="f-left-right50" style="width:150px;margin-top:10px;">ID{{ruleForm.id}}</div>
            <div class="f-left-right50">
              <el-form-item label="" prop="userName">
                <el-input v-model="ruleForm.userName" placeholder="" size="mini"></el-input>
              </el-form-item>
            </div>
            <div class="f-left-right50">
              <el-form-item label="" prop="nickName">
                <el-input v-model="ruleForm.nickName" placeholder="" size="mini"></el-input>
              </el-form-item>
            </div>
          </div>
          <div class="w-clear">
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="" prop="userType">
                <el-select v-model="ruleForm.userType" placeholder="" size="mini">
                  <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
            </div>
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="" prop="isAnswerer">
                <el-radio-group v-model="ruleForm.isAnswerer">
                  <el-radio label="1"></el-radio>
                  <el-radio label="0"></el-radio>
                </el-radio-group>
              </el-form-item>
            </div>
          </div>
          <div class="w-clear">
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="" prop="phone">
                <el-input v-model.number="ruleForm.phone" size="mini"></el-input>
              </el-form-item>
            </div>
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="" prop="email">
                <el-input v-model="ruleForm.email" size="mini"></el-input>
              </el-form-item>
            </div>
          </div>
          <div class="w-clear">
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="" prop="company">
                <el-input v-model="ruleForm.company" size="mini"></el-input>
              </el-form-item>
            </div>
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="" prop="job">
                <el-input v-model="ruleForm.job" size="mini"></el-input>
              </el-form-item>
            </div>
          </div>
          <div class="w-clear">
            <div></div>
            <div>
              <el-form-item label="" prop="details" label-width="0px">
                <el-input type="textarea" v-model="ruleForm.details" :rows="3"></el-input>
              </el-form-item>
            </div>
          </div>
          <div class="w-clear">
            <div></div>
            <div>
              <el-form-item label="" prop="content" label-width="0px">
                <el-input type="textarea" v-model="ruleForm.content" :rows="3"></el-input>
              </el-form-item>
            </div>
          </div>
          <div class="w-clear">
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="" prop="status">
                <el-radio-group v-model="ruleForm.status">
                  <el-radio label="0"></el-radio>
                  <el-radio label="1"></el-radio>
                </el-radio-group>
              </el-form-item>
            </div>
            <div class="f-left-right50" style="width:40%;">
              <el-form-item label="" required>
                <el-form-item prop="ndate">
                  <el-date-picker type="date" placeholder="" v-model="ruleForm.ndate" style="width: 100%;" value-format="yyyy-mm-dd"></el-date-picker>
                </el-form-item>
              </el-form-item>
            </div>
          </div>
          <div class="w-clear">
            <div></div>
            <div>
              <el-form-item label="" prop="remark" label-width="0px">
                <el-input type="textarea" v-model="ruleForm.remark" :rows="3"></el-input>
              </el-form-item>
            </div>
          </div>
        </el-form>

        </div>
        <span slot="footer" class="dialog-footer">

          <div v-if="lock">
            <el-button @click="centerDialogVisible = false"> </el-button>
            <el-button type="primary" @click="submitForm('ruleForm')"> </el-button>
          </div>
          <div v-if="!lock">
            <el-button @click="centerDialogVisible = false"> </el-button>
            <el-button type="primary" @click="fn_add"> </el-button>
            <el-button type="danger" @click="fn_check(2)"> </el-button>
            <el-button type="success" @click="fn_check(1)"> </el-button>
          </div>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
export default {
  props: {
    mainHeight: [Number, String],
    userType: Array
  },
  data() {
    return {
      Url: 'http://int.answer.updrv.com',
      lock: true,
      dialogTiele: '',
      currentPage: 1, // 
      maxPage: 100,   // 
      pageArr: 10, // 
      tableData: [],
      newTableData: [],
      centerDialogVisible: false,
      multipleSelection: [],
      keywords: '',
      options: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: ''
        },
        {
          value: '2',
          label: ''
        },
        {
          value: '3',
          label: ''
        },
        {
          value: '4',
          label: ''
        }
      ],
      options2: [
        {
          value: '0',
          label: ''
        },
        {
          value: '1',
          label: ''
        },
        {
          value: '2',
          label: ''
        },
        {
          value: '3',
          label: ''
        },
        {
          value: '4',
          label: ''
        },
        {
          value: '5',
          label: '  '
        },
        {
          value: '6',
          label: ''
        },
      ],
      value: '', // select
      id: '',
      ruleForm: {
        userType: '',
        id: '',
        userName: '',
        nickName: '',
        phone: '',
        email: '',
        isAnswerer: '0',
        status: '0',
        ndate: '',
        content: '',
        company: '',
        job: '',
        remark: '',
        details: ''
      },
      rules: {
        userType: [
          { required: true, message: '', trigger: 'change' }
        ],
        userName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        nickName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /\d{11}/, message: '', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^([a-z0-9_\.-]+)@([\da-z\.-]+)\.([a-z\.]{2,6})$/, message: '', trigger: 'blur' }
        ],
        isAnswerer: [
          { required: true, message: '', trigger: 'change' }
        ],
        status: [
          { required: true, message: '', trigger: 'change' }
        ],
        ndate: [
          { required: true, message: '', trigger: 'change' }
        ],
        content: [
          { required: true, message: '', trigger: 'blur' }
        ],
        company: [
          { required: true, message: '', trigger: 'blur' }
        ],
        job: [
          { required: true, message: '', trigger: 'blur' }
        ],
        remark: [
          { required: false, message: '', trigger: 'blur' }
        ],
        details: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    openEditDialog(row) {
      console.log(row)
    },
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    handleSizeChange(val) {
      console.log(` ${val} `)
      this.pageArr = val
      this.fn_loadTableList()
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`)
      this.currentPage = val
      this.fn_loadTableList()
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.fn_add()
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    redact(rows) {
      console.log(rows);
      this.dialogTiele = ''
      this.lock = false
      this.ruleForm.id = rows.id
      this.ruleForm.userType = rows.userType + ''  // select
      this.ruleForm.userName = rows.userName
      this.ruleForm.nickName = rows.nickName
      this.ruleForm.phone = rows.phone
      this.ruleForm.email = rows.email
      this.ruleForm.isAnswerer = rows.isAnswerer + ''
      this.ruleForm.status = rows.status + ''
      this.ruleForm.ndate = rows.addTime
      this.ruleForm.content = rows.content
      this.ruleForm.company = rows.company
      this.ruleForm.job = rows.job
      this.ruleForm.remark = rows.remark
      this.ruleForm.details = rows.details


      this.centerDialogVisible = true
    },
    fn_add() {
      this.post("/json/add", this.ruleForm, (data) => {
        if(data && data.status == 1){
          this.fn_loadTableList()
          this.centerDialogVisible = false
        }
        

      });
    },
    fn_delete(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        var _this = this

        this.post("/json/del", {
          id
        }, (data) => {
          _this.fn_loadTableList()

        });

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    fn_deletes() {
      if (this.multipleSelection.length > 0) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var _this = this
          var id = []
          for (var i = 0, y = this.multipleSelection.length; i < y; i++) {
            id.push(this.multipleSelection[i].id)
          }
          this.post("/json/user/dellist", {
            id
          }, (data) => {
            _this.fn_loadTableList()

          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '~'
        });
      }
    },
    fn_loadTableList() {
      var _this = this
      var keyword = this.keywords, pageNum = this.currentPage, pageSize = this.pageArr, status = this.value
      this.post("/json/userList", {
        pageNum, // 
        pageSize, // 
        status, //   0   1   2  3  4 
        keyword
      }, (data) => {
        _this.tableData = data.data
        _this.maxPage = data.totalPage

      });
    },
    hide() {
      this.lock = true
      this.dialogTiele = ''
      this.ruleForm.id = ''
      this.ruleForm.userType = ''  // select
      this.ruleForm.userName = ''
      this.ruleForm.nickName = ''
      this.ruleForm.phone = ''
      this.ruleForm.email = ''
      this.ruleForm.isAnswerer = ''
      this.ruleForm.status = ''
      this.ruleForm.ndate = ''
      this.ruleForm.content = ''
      this.ruleForm.company = ''
      this.ruleForm.job = ''
      this.ruleForm.remark = ''
      this.ruleForm.details = ''
    },
    fn_check(isAudit) {
      var _this = this
      var id = this.ruleForm.id
      this.post("json/isAudit", {
        id,
        isAudit
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false

      });
    },
    excel() {
      window.location.href = 'http://int.answer.updrv.com/json/excel'
    }
  },
  mounted() {
    window.vue = this
    this.fn_loadTableList()
  }
}
</script>
<style scoped lang="scss">
.w-clear:after {
  display: block;
  content: '';
  clear: both;
}

.mb15 {
  margin-bottom: 15px;
}

.f-right {
  float: right;
}

.dialog-centern-box {
  // width: 200px;
  .dialog-top {
    float: left;
    width: 100%;
    .dialog-left {
      float: left;
      width: 50%;
      margin-right: 5%;
    }
    ;
    .dialog-right {
      float: left;
      width: 45%;
    }
    ;
    .dialog-left {
      .user-id-box {
        float: left;
        width: 35%;
        margin-right: 5%;
      }
      ;
      .user-name-box {
        float: left;
        width: 60%;
      }
    }
  }
}
</style>



