import Vue from 'vue'
import Router from 'vue-router'
import home from '@/code/home'
import layoutMenu from '@/code/layoutMenu'
import login from '@/code/index'
import ErrorTo from '@/code/404'
import accountManage from '@/code/userManage/accountManage'
import newJuSe from '@/code/userManage/newJuSe'
import newQuanXian from '@/code/userManage/newQuanXian'
import allAnswer from '@/code/contentManage/allAnswer'
import allQuestion from '@/code/contentManage/allQuestion'
import enterpriseQuiz from '@/code/contentManage/enterpriseQuiz'
import specialtyAnswer from '@/code/contentManage/specialtyAnswer'
import sensitiveWordAudit from '@/code/jiaoYanManage/sensitiveWordAudit'
import sensitiveWordManage from '@/code/jiaoYanManage/sensitiveWordManage'
import blacklistContent from '@/code/blacklist/blacklistContent'
import blacklistUser from '@/code/blacklist/blacklistUser'
import costSUm from '@/code/caiWuManage/costSUm'
import enterpriseUserManage from '@/code/caiWuManage/enterpriseUserManage'
import generalize from '@/code/caiWuManage/generalize'
import mytasklist from '@/code/my/mytasklist'
import passwordedit from '@/code/my/passwordedit'

Vue.use(Router)



export default new Router({
  mode: 'hash',
  routes: [{
      path: '/',
      name: '',
      component: login,
    },
    {
      path: '/login',
      name: '',
      component: login,
    },
    {
      path: '/home',
      name: '',
      component: home,
      children: [{
          path: '/', //
          name: 'layoutMenu',
          component: layoutMenu
        }, {
          path: '/home', //
          name: 'layoutMenu',
          component: layoutMenu
        }, {

          path: '/accountManage', //
          name: '',
          component: accountManage
        }, {
          path: '/newJuSe', //
          name: '',
          component: newJuSe
        }, {
          path: '/newQuanXian', //
          name: '',
          component: newQuanXian
        }, {
          path: '/allAnswer', //
          name: '',
          component: allAnswer
        }, {
          path: '/allQuestion', //
          name: '',
          component: allQuestion
        }, {
          path: '/enterpriseQuiz', //
          name: '',
          component: enterpriseQuiz
        }, {
          path: '/specialtyAnswer', //
          name: '',
          component: specialtyAnswer
        }, {
          path: '/sensitiveWordManage', //
          name: '',
          component: sensitiveWordManage
        }, {
          path: '/sensitiveWordAudit', //
          name: '',
          component: sensitiveWordAudit
        }, {
          path: '/mytasklist', //
          name: '',
          component: mytasklist
        }, {
          path: '/passwordedit', //
          name: '',
          component: passwordedit
        }, {
          path: '/blacklistContent', //
          name: '',
          component: blacklistContent
        }, {
          path: '/blacklistUser', //
          name: '',
          component: blacklistUser
        }, {
          path: '/costSUm', //
          name: '',
          component: costSUm
        }, {
          path: '/enterpriseUserManage', //
          name: '',
          component: enterpriseUserManage
        },
        {
          path: '/generalize', //
          name: '',
          component: generalize
        },
        {
          path: '/*', //
          name: '404',
          component: ErrorTo
        }
      ]
    }
  ]
})

