<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:400px;float:left;">
            <el-input v-model="keywords" suffix-icon="el-icon-search" placeholder=""></el-input>
          </div>
          <div style="width:70px;float:left;margin-left:10px;">
            <el-button type="primary" icon="el-icon-search"></el-button>
          </div>
          <div style="width:230px;float:right;">
            <el-button type="primary" @click="centerDialogVisible=true,dialogTitle='()'"></el-button>
            <el-button type="danger" @click="fn_deletes"></el-button>
          </div>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" :max-height='mainHeight-132'>
              <el-table-column type="selection" width="55">
              </el-table-column>
              <el-table-column prop="id" label="ID" width="120">
              </el-table-column>
              <el-table-column prop="userID" label="ID" width="120">
              </el-table-column>
              <el-table-column prop="userName" label="" width="140">
              </el-table-column>
              <el-table-column prop="qyID" label="ID" width="120">
              </el-table-column>
              <el-table-column prop="qID" label="ID" width="120">
              </el-table-column>
              <el-table-column prop="title" label="" width="340">
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="props">
                  <div :title='props.row.content' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.content}}</div>
                </template>
              </el-table-column>
              <el-table-column label="" width="80">
                <template slot-scope="props">
                  {{ props.row.islike == 1? "" : "" }}
                </template>
              </el-table-column>
              <el-table-column prop="ndate" label="" width="120">
              </el-table-column>
              <el-table-column label="" width="100">
                <template slot-scope="props">
                  <!-- {{ props.row.suerType == 1? "" : "" }} -->
                  <el-tag type="success" v-if='(props.row.status==1)' size='small'></el-tag>
                  <el-tag type="danger" v-else size='small'></el-tag>
                </template>
              </el-table-column>
              <el-table-column label="" width="90" class-name='noneborder'>
                <template slot-scope="props">
                  <div style="margin-bottom:10px;">
                    <el-button size="mini" @click="fn_check(props.row)" type="primary"></el-button>
                  </div>
                  <div>
                    <el-button @click="fn_look(props.row)" size="mini" type="info"></el-button>
                  </div>
                </template>
              </el-table-column>

            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 50, 100]" :page-size="10" layout="total, sizes, prev, pager, next" :total="maxPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <el-dialog :title="dialogTitle" :visible.sync="centerDialogVisible" width="1000px" style='text-align:left' @close="initialize" :close-on-click-modal='false'>
        <div>
          <div class="w-clear mb15">
            <div class="f-left-right50" style="width:190px;">ID{{id}}</div>
            <div class="f-left-right50">
              <div class="f-left">ID</div>
              <div class="f-left">
                <el-input type="text" v-model="userID" size="mini" placeholder="" :disabled='zhiDu'></el-input>
              </div>
            </div>
            <div class="f-left">
              <div class="f-left"></div>
              <div class="f-left">
                <el-input type="text" v-model="userName" size="mini" placeholder="" :disabled='zhiDu'></el-input>
              </div>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left-right20" style="width:220px;">
              <div class="f-left">ID</div>
              <div class="f-left" style="width:136px;">
                <el-input type="text" v-model="qyID" size="mini" placeholder="" :disabled='zhiDu'></el-input>
              </div>
            </div>
            <div class="f-left-right20" style="width:293px;">
              <div class="f-left">ID</div>
              <div class="f-left">
                <el-input type="text" v-model="qID" size="mini" placeholder="" :disabled='zhiDu'></el-input>
              </div>
            </div>
            <div class="f-left">
              <div class="f-left">
                
              </div>
              <div class="f-left" style="width:250px;">
                <el-input type="text" v-model="title" placeholder="" size="mini" :disabled='zhiDu'></el-input>
              </div>
            </div>
          </div>
          <div class="w-clear mb15">
            <div></div>
            <div>
              <el-input type="textarea" :rows="4" placeholder="" v-model="content" :disabled='zhiDu'></el-input>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left">
              
            </div>
            <div class="f-left">
              <template>
                <el-radio v-model="islike" label="1" :disabled='zhiDu'></el-radio>
                <el-radio v-model="islike" label="0" :disabled='zhiDu'></el-radio>
              </template>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left-right50">
              <div class="f-left"></div>
              <div class="f-left">
                <div class="block">
                  <el-date-picker v-model="ndate" align="right" type="date" size="mini" placeholder="" :disabled='zhiDu'>
                  </el-date-picker>
                </div>
              </div>
            </div>
            <div class="f-left-right">
              <div class="f-left">
                
              </div>
              <div class="f-left">
                <template>
                  <!-- {{ props.row.suerType == 1? "" : "" }} -->
                  <el-tag type="success" v-if='(status==1)' size='medium'></el-tag>
                  <el-tag type="danger" v-else size='medium'></el-tag>
                </template>
              </div>
            </div>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <div v-if="lock">
            <el-button @click="centerDialogVisible = false"> </el-button>
            <el-button v-if="add2" type="primary" @click="fn_add"> </el-button>
          </div>
          <div v-if="!lock">
            <el-button @click="centerDialogVisible = false"> </el-button>
            <el-button type="info" @click="centerDialogVisible = false"> </el-button>
            <el-button type="danger" @click="centerDialogVisible = false"> </el-button>
            <el-button type="primary" @click="centerDialogVisible = false"> </el-button>
          </div>
        </span>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
export default {
  props: {
    mainHeight: [Number, String],
    userType: Array
  },
  data() {
    return {
      dialogTitle: '',
      add2: true,
      currentPage: 1, // 
      maxPage: 100,   // 
      pageArr: 10, // 
      centerDialogVisible: false,
      lock: true,
      zhiDu: false,
      tableData: [],
      multipleSelection: [],
      keywords: '',
      formData: {},
      id: '', //id
      userID: '', //id
      userName: '',//
      qyID: '',//id
      qID: '',//id
      title: '', //
      content: '',//
      islike: '',  //  0   1 
      ndate: '',  //
      status: '1'  //  0  1
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    handleSizeChange(val) {
      console.log(` ${val} `)
      this.pageArr = val
      this.fn_loadTableList()
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`)
      this.currentPage = val
      this.fn_loadTableList()
    },
    openEditDialog(row) {
      console.log(row)
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    initialize() { //  
      this.zhiDu = false
      this.lock = true
      this.add2 = true
      this.id = '' //id
      this.userID = '' //id
      this.userName = ''//
      this.qyID = ''//id
      this.qID = ''//id
      this.title = '' //
      this.content = ''//
      this.islike = '1'  //  0   1 
      this.ndate = ''  //
      this.status = '1'  //  0  1
    },
    fn_deletes() {
      if (this.multipleSelection.length > 0) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '~'
        });
      }
    },
    fn_check(data) {
      this.dialogTitle = ''
      this.lock = false
      this.centerDialogVisible = true
      this.id = data.id //id
      this.userID = data.userID //id
      this.userName = data.userName//
      this.qyID = data.qyID//id
      this.qID = data.qID//id
      this.title = data.title //
      this.content = data.content//
      this.islike = data.islike + '' //  0   1 
      this.ndate = data.ndate  //
      this.status = data.status + ''  //  0  1
    },
    fn_look(data) {
      this.dialogTitle = ''
      this.lock = true
      this.add2 = false
      this.centerDialogVisible = true
      this.id = data.id //id
      this.userID = data.userID //id
      this.userName = data.userName//
      this.qyID = data.qyID//id
      this.qID = data.qID//id
      this.title = data.title //
      this.content = data.content//
      this.islike = data.islike + '' //  0   1 
      this.ndate = data.ndate  //
      this.status = data.status + ''  //  0  1
    },
    fn_loadTableList() {
      var _this = this
      var keyword = this.keywords, pageNum = this.currentPage, pageSize = this.pageArr
      this.post("/json/majorList", {
        keyword,
        pageNum,
        pageSize
      }, (data) => {
        _this.tableData = data.data
        _this.maxPage = data.totalPage
      })
    },
    fn_add() {
      var _this = this
      // var keyword = this.keywords, pageNum = this.currentPage
      var userName = this.userName,
        title = this.title,
        content = this.content,
        ndate = this.ndate
      this.post("/json/majorAdd", {
        userName, // 
        title, // 
        content, // 
        ndate // 
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_check_to(status) {
      var _this = this
      var id = this.id
      this.post("/json/answerAudit", {
        id,
        status
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_deletes() {
      if (this.multipleSelection.length > 0) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var _this = this
          var id = []
          for (var i = 0, y = this.multipleSelection.length; i < y; i++) {
            id.push(this.multipleSelection[i].id)
          }
          this.post("/json/dellist", {
            id
          }, (data) => {
            _this.fn_loadTableList()
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '~'
        });
      }
    }
  },
  mounted() {
    window.vue = this
    this.fn_loadTableList()
  }
}
</script>
<style lang="scss" scoped>
  .box-orient{
    -webkit-box-orient: vertical;
  }
</style>


