<template>
  <el-container>
    <el-main>
      <div class="main-box">
        <div class="function-box">
          <div style="width:400px;float:left;">
            <el-input v-model="keywords" suffix-icon="el-icon-search" placeholder=""></el-input>
          </div>
          <div style="width:70px;float:left;margin-left:10px;">
            <el-button type="primary" icon="el-icon-search" @click="fn_loadTableList()"></el-button>
          </div>
          <div style="width:340px;float:right;">
            <el-button type="primary" @click="centerDialogVisible = true,dialogTiele='()'"></el-button>
            <el-button type="danger" @click="fn_repulse"></el-button>
            <el-button type="info" @click="fn_blacklist"></el-button>
          </div>
        </div>
        <div class="table">
          <template>
            <el-table ref="multipleTable" :data="tableData" border tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange"
            :max-height='mainHeight-132'>
              <el-table-column type="selection" width="55">
              </el-table-column>
              <el-table-column prop="id" label="ID" width="80">
              </el-table-column>
              <el-table-column prop="qID" label="ID" width="100">
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="props">
                  <div :title='props.row.title' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.title}}</div>
                </template>
              </el-table-column>
              <el-table-column prop="userID" label="ID" width="160">
              </el-table-column>
              <el-table-column prop="userName" label="" width="160">
              </el-table-column>
              <el-table-column label="" width="120">
                <template slot-scope="props">
                  {{props.row.isAnonymous==0?"":""}}
                </template>
              </el-table-column>
              <el-table-column label="">
                <template slot-scope="props">
                  <div :title='props.row.content' class="row3" style='-webkit-box-orient: vertical;'>{{props.row.content}}</div>
                </template>
              </el-table-column>
              <el-table-column prop="viewCount" label="" width="80">
              </el-table-column>
              <el-table-column prop="userlikeCount" label="" width="80">
              </el-table-column>
              <el-table-column prop="userIDlike" label="ID" width="100">
              </el-table-column>
              <el-table-column prop="ndate" label="" width="100">
              </el-table-column>
              <el-table-column label="" width="80">
                <template slot-scope="props">
                  <!-- {{ props.row.suerType == 1? "" : "" }} -->
                  <el-tag type="success" v-if='(props.row.status==1)' size='small'></el-tag>
                  <el-tag type="danger" v-else size='small'></el-tag>
                </template>
              </el-table-column>
              <el-table-column label="" width="120" class-name='noneborder'>
                <template slot-scope="props">
                  <div style="margin-bottom:10px;">
                    <el-button size="mini" @click="fn_check(props.row)" type="primary"></el-button>
                  </div>
                  <div>
                    <el-button @click="fn_look(props.row)" size="mini" type="info"></el-button>
                  </div>
                </template>
              </el-table-column>
            </el-table>
          </template>
        </div>
        <div class="w-clear page-color">
          <div class="f-right" style="">
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 50, 100]" :page-size="10" layout="total, sizes, prev, pager, next" :total="maxPage">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
      <el-dialog :title="dialogTitle" :visible.sync="centerDialogVisible" width="1000px" style='text-align:left' @close="hide" :close-on-click-modal='false'>
        <div>
          <div class="w-clear mb15">
            <div class="f-left-right50" style="width:150px;">ID{{id}}</div>
            <div class="f-left-right50">
              <div class="f-left">ID</div>
              <div class="f-left">
                <el-input v-model="qID" size="mini" placeholder="" :disabled='zhiDu'></el-input>
              </div>
            </div>
            <div class="f-left-right50">
              <div class="f-left"></div>
              <div class="f-left" style="width:300px;">
                <el-input v-model="title" size="mini" placeholder="" :disabled='zhiDu'></el-input>
              </div>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left-right50">
              <div class="f-left">
                ID
              </div>
              <div class="f-left">
                <el-input v-model="userID" placeholder="" size="mini" :disabled='zhiDu'></el-input>
              </div>
            </div>
            <div class="f-left-right50">
              <div class="f-left">
                
              </div>
              <div class="f-left" style="width:260px;">
                <el-input v-model="userName" placeholder="" size="mini" :disabled='zhiDu'></el-input>
              </div>
            </div>
            <div class="f-left-right50">
              <div class="f-left">
                
              </div>
              <div class="f-left">
                <el-radio v-model="isAnonymous" label="0" :disabled='zhiDu'></el-radio>
                <el-radio v-model="isAnonymous" label="1" :disabled='zhiDu'></el-radio>
              </div>
            </div>
          </div>
          <div class="w-clear mb15">
            <div>
              
            </div>
            <div>
              <el-input type="textarea" v-model="content" :rows="3" placeholder="" :disabled='zhiDu'></el-input>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left-right50">
              {{viewCount}}
            </div>
            <div class="f-left-right50">
              {{userlikeCount}}
            </div>
          </div>
          <div class="w-clear mb15">
            <div>
              ID
            </div>
            <div>
              <el-input type="textarea" v-model="userIDlike" :rows="3" placeholder="" :disabled='zhiDu'></el-input>
            </div>
          </div>
          <div class="w-clear mb15">
            <div class="f-left" style="width:620px;">
              <div class="f-left"></div>
              <div class="block f-left">
                <el-date-picker v-model="ndate" align="right" type="date" placeholder="" size="mini" value-format="yyyy-MM-dd" :disabled="zhiDu">
                </el-date-picker>
              </div>
            </div>
            <div class="f-left">
              <div class="f-left">
                
              </div>
              <div style="float:left;">
                <template>
                  <el-tag type="success" v-if='(status==1)' size='small'></el-tag>
                  <el-tag type="danger" v-else size='small'></el-tag>
                </template>

              </div>
            </div>
          </div>
        </div>
        <span slot="footer" class="dialog-footer">
          <div v-if="add">
            <el-button type="" @click="centerDialogVisible = false"> </el-button>
            <el-button v-if="add2" type="primary" @click=" fn_add()"> </el-button>
          </div>
          <div v-if="!add">
            <el-button type="" @click="centerDialogVisible = false"> </el-button>
            <el-button type="info" @click=" fn_black()"> </el-button>
            <el-button type="danger" @click="fn_check_to(0)"> </el-button>
            <el-button type="primary" @click="fn_check_to(1)"> </el-button>
          </div>
        </span>
      </el-dialog>
    </el-main>

  </el-container>
</template>
<script>
export default {
  props:{
    mainHeight: [Number,String],
    userType: Array
  },
  data() {
    return {
      Url: 'http://int.answer.updrv.com',
      add: true,
      add2: true,
      zhiDu: false,
      dialogTitle: '',
      currentPage: 1, // 
      maxPage: 100,   // 
      pageArr: 10, // 
      initial: 20, // 
      centerDialogVisible: false,
      tableData: [],
      multipleSelection: [],
      keywords: '',
      options: [{
        value: '0',
        label: ''
      }, {
        value: '1',
        label: ''
      }],
      value: '',
      id: '',  //id
      qID: '', //id
      title: '', //
      userID: '',     //id
      userName: '', //
      isAnonymous: '', //  0   1
      content: '', //
      viewCount: '',  //
      userlikeCount: '',//
      userIDlike: '', //id
      ndate: '', //
      status: 1  //   0  1 

    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    handleSizeChange(val) {
      console.log(` ${val} `)
      this.pageArr = val
      this.fn_loadTableList()
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`)
      this.currentPage = val
      this.fn_loadTableList()
    },
    openEditDialog(row) {
      console.log(row)
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    hide() {
      this.zhiDu = false
      this.add = true
      this.add2 = true
      this.id = ''  //id
      this.qID = '' //id
      this.title = '' //
      this.userID = ''     //id
      this.userName = '' //
      this.isAnonymous = '' //  0   1
      this.content = '' //
      this.viewCount = ''  //
      this.userlikeCount = ''//
      this.userIDlike = '' //id
      this.ndate = '' //
      this.status = 1  //   0  1 
    },
    fn_repulse() {
      if (this.multipleSelection.length > 0) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var _this = this
          var id = []
          for (var i = 0, y = this.multipleSelection.length; i < y; i++) {
            id.push(this.multipleSelection[i].id)
          }
          this.post("/json/answerAuditlist", {
            id
          }, (data) => {
            _this.fn_loadTableList()
            _this.centerDialogVisible = false
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '~'
        });
      }
    },
    fn_blacklist() {
      if (this.multipleSelection.length > 0) {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var _this = this
          var id = []
          for (var i = 0, y = this.multipleSelection.length; i < y; i++) {
            id.push(this.multipleSelection[i].id)
          }
          this.post("/json/backanswerList", {
            id
          }, (data) => {
            _this.fn_loadTableList()
            _this.centerDialogVisible = false
          })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      } else {
        this.$message({
          type: 'info',
          message: '~'
        });
      }
    },
    fn_check(data) {
      this.add = false
      this.centerDialogVisible = true
      this.dialogTitle = ''
      console.log(data)
      this.id = data.id  //id
      this.qID = data.qID //id
      this.title = data.title //
      this.userID = data.userID     //id
      this.userName = data.userName //
      this.isAnonymous = data.isAnonymous + '' //  0   1
      this.content = data.content //
      this.viewCount = data.viewCount  //
      this.userlikeCount = data.userlikeCount//
      this.userIDlike = data.userIDlike //id
      this.ndate = data.ndate //
      this.status = data.status  //   0  1 
    },
    fn_look(data) {
      this.dialogTitle = ''
      this.zhiDu = true
      this.add2 = false
      this.centerDialogVisible = true
      console.log(data)
      this.id = data.id  //id
      this.qID = data.qID //id
      this.title = data.title //
      this.userID = data.userID     //id
      this.userName = data.userName //
      this.isAnonymous = data.isAnonymous +'' //  0   1
      this.content = data.content //
      this.viewCount = data.viewCount  //
      this.userlikeCount = data.userlikeCount//
      this.userIDlike = data.userIDlike //id
      this.ndate = data.ndate //
      this.status = data.status  //   0  1 
    },
    fn_check_to(status) {
      var _this = this
      var id = this.id
      this.post("/json/answerAudit", {
        id,
        status
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_black() {
      var _this = this
      var id = this.id
      this.post("/json/backanswer", {
        id
      }, (data) => {
        _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    },
    fn_loadTableList() {
      var _this = this
      var keyword = this.keywords, pageNum = this.currentPage, pageSize = this.pageArr
      this.post("/json/answerList", {
        keyword,
        pageNum,
        pageSize
      }, (data) => {
        _this.tableData = data.data
        _this.maxPage = data.totalPage
      })
    },
    fn_add() {
      var _this = this
      var title = this.title, userName = this.userName, content = this.content
      this.post("/json/answerAdd", {
         title,
        userName,
        content
      }, (data) => {
         _this.fn_loadTableList()
        _this.centerDialogVisible = false
      })
    }
  },
  mounted() {
    // console.log(1)
    window.vue = this
    this.fn_loadTableList()
  }
}
</script>
<style lang="scss" scoped>
  .box-orient{
    -webkit-box-orient: vertical;
  }
</style>


